@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;600&display=swap);
.navbar{
    position: absolute;
    top: 0;
    left: 0;
    max-height: 50px;
    height: 100%;
    width: 100%;
    background: lightcoral;
}

.title{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    color: lightcyan;
    font-size: 1.5rem;
    text-transform: uppercase;
    font-weight: 600;
}
.card{
    height: 200px;
    width: 200px;
    border-radius: 10px;
    background: lightsteelblue;
    /* Position */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    /* Box Shadow */
    box-shadow: -2px -2px 10px rgba(255,255, 255, 0.3),
                -5px -5px 10px rgba(0,0, 0, 0.1) inset;
    margin: 25px;
}

.name{
    font-size: 2rem;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: 2px;
    color: rgba(0,0, 0, 0.8);
    margin: 10px 0px;
}
.value{
    font-size: 1rem;
    font-weight: 600;
    letter-spacing: 2px;
    color: rgba(0,0, 0, 0.5);
    margin: 10px 0px;
}
.content{
    display: grid;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.content-box{
    display: grid;
    /* justify-content: center;
    align-items: center; */
    grid-template-columns: 1fr 1fr;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
    font-family: 'Montserrat', sans-serif;
}
